#header {
  position: relative;
  background-color: #292a2d;
  width: 100%;
}

#header .header_bg {
  position: relative;
  height: 160px;
  border-bottom: 1px solid #3a3b3d;
}

#header .group_flex {
  position: relative;
  width: 1130px;
  height: 100%;
  margin: 0 auto;
  padding: 0 30px;
}

#header .logo_area {
  position: relative;
  height: 100%;
}

#header .logo {
  position: absolute;
  top: 12px;
  left: 0;
  margin-top: 0px;
  font-size: 85px;
}

#header .search_area {
  position: absolute;
  top: 43%;
  left: 268px;
  margin-top: -27px;
}

#header .search_area .search_space {
  position: relative;
  width: 700px;
  height: 60px;
  border-width: 2px;
  border-style: solid;
  border-color: #e1aa71;
  border-radius: 2px;
}

#header .search_area .se_select {
  position: absolute;
  /* width: 79px; */
  height: 100%;
  background-color: #292a2d;
  color: #d9d9d9;
  font-weight: 600;
  border-right: #e1aa71;
  outline: none;
}

#header .header_bg .search_area .input_text {
  margin-left: 80px;
  width: 500px;
  height: 34px;
  padding: 13px;
  margin-left: 80px;
  font-size: 20px;
  line-height: 24px;
  font-weight: 700;
  outline: 0;
  background-color: #292a2d;
  color: #d9d9d9;
  border-left-style: solid;
  border-left-color: #e1aa71;
  border-left-width: 2px;
}

#header .search_area .btn_search_submit {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 70px;
  background-color: #e1aa71;
  border-radius: 0 1px 1px 0;
  cursor: pointer;
  outline: none;
}

#header .header_bg .group_flex .emoticon {
  position: absolute;
  top: 20px;
  right: 40px;
}

#header .header_bg .group_flex .emoticon img {
  width: 150px;
  height: 120px;
  border-radius: 40%;
}

#gnb {
  position: relative;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.12);
}

#gnb .gnb_inner {
  position: relative;
  width: 1130px;
  padding: 0 30px;
  margin: 0 auto;
}

#gnb .group_nav {
  position: relative;
  overflow: hidden;
  padding: 0;
}

#gnb .list_nav {
  float: left;
  font-size: 15px;
  font-weight: 700;
  letter-spacing: -0.3px;
  margin-block-start: 0px;
  margin-block-end: 0px;
}

#gnb .list_nav .nav_item {
  position: relative;
  float: left;
  width: 90px;
  height: 40px;
  box-sizing: border-box;
  text-align: center;
  padding: 0 14px;
}

#header #gnb .gnb_inner .group_nav .list_nav .nav_item.nav_item_select {
  background-color: rgb(218, 217, 217);
  color: black;
  border-bottom: 5px solid rgb(100, 99, 99);
}

/* #gnb .list_nav .nav_item + .nav_item {
    margin-left: 30px;
  } */

#gnb .list_nav .nav_item a {
  position: absolute;
  top: 9px;
  left: 0px;
  width: 90px;
  height: 40px;
}

#header #gnb .gnb_inner .group_nav .list_nav .nav_item:hover {
  /* background-color: rgb(218, 217, 217);
    color: black; */

  border-bottom: 5px solid rgb(218, 217, 217);
}
