#container .column_fix_wrap .login_box {
  border: 1px solid #3a3b3d;
  width: 348px;
  height: 100px;
  background-color: #2c2d30;
}

#container .column_fix_wrap .login_box .login_wrap {
  position: relative;
  padding: 0 20px;
}

#container .column_fix_wrap .login_box .login_wrap .login_form {
  position: absolute;
  top: 10px;
}

#container .column_fix_wrap .login_box .login_wrap .login_form input {
  border-radius: 5px;
  outline: none;
  width: 120px;
  line-height: 20px;
  padding: 3px 10px;
  border: 1px solid #e1aa71;
  background-color: #1e1f21;
  color: #fff;
  margin-top: 8px;
}

#container .column_fix_wrap .login_box .login_wrap .login_form .input_email {
  margin-left: 5px;
}

#container .column_fix_wrap .login_box .login_wrap .login_form .input_password {
  margin-left: 10px;
}

#container .column_fix_wrap .login_box .login_wrap button {
  position: absolute;
  top: 48px;
  background-color: #1e1f21;
  font-size: 11px;
  font-weight: 600;
  padding: 6px 12px;
  text-decoration: none;
  color: #fff;
  border-radius: 14px;
  cursor: pointer;
  outline: none;
}

#container .column_fix_wrap .login_box .login_wrap .login_form a {
  position: absolute;
  top: 48px;
  background-color: #1e1f21;
  font-size: 11px;
  font-weight: 600;
  padding: 6px 10px;
  text-decoration: none;
  color: #fff;
  border-radius: 14px;
}

#container
  .column_fix_wrap
  .login_box
  .login_wrap
  .login_form
  .btn_login_local {
  left: 5px;
  background-color: #4063bd;
}

#container
  .column_fix_wrap
  .login_box
  .login_wrap
  .login_form
  .btn_login_naver {
  left: 72px;
  background-color: #19ce60;
}

#container
  .column_fix_wrap
  .login_box
  .login_wrap
  .login_form
  .btn_login_google {
  left: 134px;
  background-color: rgb(235, 72, 51);
}

#container .column_fix_wrap .login_box .login_wrap .btn_find {
  border: 1px solid black;
  padding: 6px 11px;
  top: 57px;
  right: 75px;
}

#container .column_fix_wrap .login_box .login_wrap .btn_signup {
  top: 58px;
  right: 32px;
  background-color: #e1aa71;
  color: black;
  padding: 5px 14px;
}
