.signup_modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 1;
}

.signup_wrap {
  position: fixed;
  top: 32%;
  left: 40%;
  padding: 20px;
  height: 182px;
  width: 310px;
  background-color: rgba(218, 217, 217, 0.9);
  color: black;
  border-radius: 3px;
}

.signup_wrap input {
  display: block;
  margin-bottom: 10px;
  padding: 3px 5px;
  width: 60%;
  border-radius: 3px;
  outline: none;
}

.signup_wrap #signup_email {
  display: inline-block;
  width: 70%;
}

.signup_wrap .confirm_email_btn,
.signup_send_email {
  display: inline-block;
  margin-left: 10%;
  cursor: pointer;
}

.signup_wrap .phase2 input,
button {
  display: inline-block;
}

.signup_wrap .phase2 input {
  width: 70%;
}

.signup_wrap .phase2 .certificate_number {
  margin-left: 10%;
  cursor: pointer;
}

.signup_wrap .signup_header h4 {
  margin-block-start: 0px;
  margin-block-end: 13px;
}

/* .signup_wrap .signup_footer {
  position: relative;
} */

.signup_footer a {
  position: absolute;
  bottom: 10px;
  font-size: 12px;
  font-weight: 500;
  padding: 6px 20px;
  text-decoration: none;
  color: rgb(250, 250, 250);
  border-radius: 14px;
}

.oauth_signup_naver {
  background-color: #53b900;
}

.oauth_signup_google {
  background-color: rgb(235, 72, 51);
  left: 75px;
}

.md_signup_btn {
  position: absolute;
  bottom: 10px;
  right: 10px;
  cursor: pointer;
}

.signup_btn {
  color: #53b900;
  right: 28px;
}
