#container .column_fix_wrap .op_gg {
  margin-top: 15px;
  margin-bottom: 119px;
  border: 1px solid #3a3b3d;
  height: 151px;
}

#container .column_fix_wrap .op_gg .OP_GG {
  position: relative;
  background-color: rgb(77, 130, 226);
  color: #fff;
  padding: 9px 9px;
  text-align: center;
  vertical-align: middle;
  font-weight: 600;
  margin: 1px;
}

#container .column_fix_wrap .op_gg .OP_GG img {
  position: absolute;
  top: 9px;
  left: 120px;
  width: 20px;
  height: 20px;
}

#container .column_fix_wrap .op_gg .search_types {
  position: relative;
  height: 30px;
  border-bottom: 1px solid #2c2d30;
}

#container .column_fix_wrap .op_gg .search_types ul {
  margin-block-start: 0px;
  margin-block-end: 0px;
}

#container .column_fix_wrap .op_gg .search_types .type_tab_active {
  border-bottom: 2px solid #fff;
  color: #fff;
}

#container .column_fix_wrap .op_gg .search_types .type_tab {
  float: left;
  border-left: 1px solid #2c2d30;
  width: 172px;
  height: 26px;
  text-align: center;
  padding-top: 4px;
  font-weight: 700;
  font-size: 15px;
  cursor: pointer;
}

#container .column_fix_wrap .op_gg .search_types .type_tab:hover {
  border-bottom: 2px solid #fff;
  color: #fff;
}

#container .column_fix_wrap .op_gg .search_box {
  position: relative;
}

#container .column_fix_wrap .op_gg .search_box .input_search {
  position: absolute;
  top: 22px;
  left: 75px;
  border-radius: 2px;
  height: 30px;
  width: 135px;
  padding-left: 15px;
  padding-right: 55px;
  outline: none;
  font-size: 15px;
  font-weight: 600;
  color: #3a3d3b;
  text-align: center;
}

#container .column_fix_wrap .op_gg .search_box .op_search_btn {
  position: absolute;
  background-color: #fff;
  color: rgb(0, 168, 249);
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  top: 28px;
  right: 74px;
  border-radius: 2px;
  outline: none;
  cursor: pointer;
}
