body {
  /* background-color: #292a2d; */
  background-color: #1e1f21;
  color: #d9d9d9;
  margin: 0;
}

button,
input,
select {
  border: 0;
  font-size: 12px;
  line-height: 16px;
  color: #202020;
}

ol,
ul {
  list-style: none;
  padding-inline-start: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

#wrap {
  overflow: hidden;
  min-width: 1190px;
}
