#container
  .movie_search_box
  .search_result
  .search_result_section
  .search_result_list
  .search_result_item.selected_item {
  background-color: #4063bd;
  color: #fff;
}

#container
  .movie_search_box
  .search_result
  .search_result_section
  .search_result_list
  .search_result_item {
  position: relative;
  border-bottom: 1px solid #3a3b3d;
  padding: 10px;
  font-weight: 700;
  cursor: pointer;
}

#container
  .movie_search_box
  .search_result
  .search_result_section
  .search_result_list
  .search_result_item:hover {
  background-color: rgb(218, 217, 217);
  color: black;
}

#container
  .movie_search_box
  .search_result
  .search_result_section
  .search_result_list
  .search_result_item
  .movie_item_poster {
  width: 70px;
  height: 75px;
}

#container
  .movie_search_box
  .search_result
  .search_result_section
  .search_result_list
  .search_result_item
  .summary_title {
  position: absolute;
  top: 0px;
  left: 90px;
  font-size: 14px;
  width: 170px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

#container
  .movie_search_box
  .search_result
  .search_result_section
  .search_result_list
  .search_result_item
  .summary_director {
  position: absolute;
  top: 40px;
  left: 90px;
  font-size: 13px;
  width: 130px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

#container
  .movie_search_box
  .search_result
  .search_result_section
  .search_result_list
  .search_result_item
  .summary_userRate {
  position: absolute;
  font-size: 13px;
  bottom: 10px;
  right: 10px;
}
