#container {
  position: relative;
  width: 1130px;
  padding: 20px 30px;
  margin: 0 auto;
  letter-spacing: -0.2px;
}

#container .column_left {
  float: left;
  width: 750px;
}

#container .column_right {
  float: right;
  width: 350px;
  /* border: 2px solid beige; */
}

#container .column_fix_wrap {
  position: relative;
  width: 350px;
}

#container::after {
  content: "";
  clear: both;
  display: table;
  table-layout: fixed;
}
