.find_modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 1;
}

.find_wrap {
  position: relative;
  top: 35%;
  left: 40%;
  padding: 20px;
  height: 153px;
  width: 310px;
  background-color: rgba(218, 217, 217, 0.9);
  color: black;
  border-radius: 3px;
}

.find_wrap input {
  display: block;
  margin-bottom: 10px;
  padding: 3px 5px;
  width: 60%;
  border-radius: 3px;
  outline: none;
}

.find_wrap #find_email {
  display: inline-block;
  width: 70%;
}

.find_wrap .find_send_email {
  margin-left: 10%;
  display: inline-block;
  cursor: pointer;
}

.find_wrap .phase2 input,
button {
  display: inline-block;
}

.find_wrap .phase2 input {
  width: 70%;
}

.find_wrap .phase2 .certificate_number {
  margin-left: 10%;
  cursor: pointer;
}

.find_wrap .find_header h4 {
  margin-block-start: 0px;
  margin-block-end: 13px;
}

.md_find_btn {
  position: absolute;
  bottom: 10px;
  right: 10px;
  cursor: pointer;
}

.find_btn {
  color: #53b900;
  right: 28px;
}

.btn_hide {
  display: none;
}
