#container .my_profile {
  width: 350px;
}

#container .my_cont {
  border: 1px solid #3a3b3d;
  height: 100px;
}

#container .my_info {
  background-color: #2c2d30;
}

#container .info_area {
  position: relative;
  overflow: hidden;
  height: 74px;
  padding: 23px 17px 3px;
}

#container .btn_logout {
  background-color: #1e1f21;
  font-size: 11px;
  position: absolute;
  top: 17px;
  right: 17px;
  padding: 6px 10px 6px 11px;
  text-decoration: none;
  color: #fff;
  border-radius: 14px;
}

#container .user_thumb {
  position: relative;
  float: left;
  margin-right: 16px;
}

#container .user_thumb > img {
  border-radius: 50%;
}

#container .user_info {
  overflow: hidden;
  margin-top: 5px;
}

#container .nickname {
  display: inline;
  overflow: hidden;
  max-width: 85px;
  vertical-align: top;
  white-space: nowrap;
  font-size: 15px;
  font-weight: 700;
}

#container .link_my {
  color: #969696;
  font-size: 11px;
}

#container .link_my::before {
  background-color: #3a3b3d;
  display: inline-block;
  width: 1px;
  height: 12px;
  margin: 0 7px 0 7px;
  content: "";
  vertical-align: -1px;
}

#container .email {
  color: #7a7a7a;
  overflow: hidden;
  padding-top: 1px;
  white-space: nowrap;
  text-overflow: ellipsis;
}
