#container .column_left .movie_box {
  position: relative;
  border: 1px solid #3a3d3b;
  margin-top: 15px;
  width: 750px;
  height: 345px;
}

#container .column_left .movie_box .movie_list_area {
  position: relative;
  background-color: #2c2d30;
  float: left;
  width: 200px;
  height: 345px;
  overflow-y: auto;
}

#container .column_left .movie_box .movie_list_area::-webkit-scrollbar {
  width: 5px;
}

#container .column_left .movie_box .movie_list_area::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #4d4d4e;
}

#container .column_left .movie_box .movie_list_area::-webkit-scrollbar-track {
  background-color: rgba(29, 23, 23, 0.63);
}

#container .column_left .movie_box .movie_list_area .movie_list {
  padding: 7px 14px 3px;
}

#container .column_left .movie_box .movie_list_area .movie_list .movie_item {
  position: relative;
  padding: 0 14px 0 18px;
  margin: 0 0 12px;
  line-height: 34px;
  height: 34px;
  background-color: #1e1f21;
  border-radius: 14px;
  cursor: pointer;
}

#container
  .column_left
  .movie_box
  .movie_list_area
  .movie_list
  .movie_item_active {
  background-color: #4063bd;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.16);
  border-radius: 14px;
  font-weight: 700;
  color: #fff;
}

#container
  .column_left
  .movie_box
  .movie_list_area
  .movie_list
  .movie_item.movie_item_inactive:hover {
  background-color: rgb(218, 217, 217);
  color: black;
}

#container
  .column_left
  .movie_box
  .movie_list_area
  .movie_list
  .movie_item
  .mitem {
  font-size: 13px;
  width: 130px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  display: block;
}

#container
  .column_left
  .movie_box
  .movie_list_area
  .movie_list
  .movie_item
  .pick_type {
  position: absolute;
  top: 11px;
  right: 10px;
}

#container .column_left .movie_box .selected_movie {
  float: left;
  padding: 25px 30px 5px 30px;
}

#container .column_left .movie_box .selected_movie .movie_hash_tag {
  line-height: 20px;
  font-size: 13px;
  font-weight: 700;
  padding-top: 20px;
}

#container .column_left .movie_box .selected_movie .about_movie {
  position: relative;
}

#container .column_left .movie_box .selected_movie .about_movie .movie_poster {
  float: left;
  text-align: center;
  margin-right: 20px;
  margin-top: 20px;
}

#container
  .column_left
  .movie_box
  .selected_movie
  .about_movie
  .movie_poster
  img {
  width: 150px;
  height: 197px;
}

#container
  .column_left
  .movie_box
  .selected_movie
  .about_movie
  .movie_poster
  h5 {
  margin-block-start: 0;
  margin-top: 5px;
  width: 150px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

#container .column_left .movie_box .selected_movie .about_movie .movie_info {
  float: left;
  width: 300px;
  height: 260px;
  font-size: 15px;
  font-weight: 500;
  margin-top: 15px;
  margin-left: 10px;
}

#container
  .column_left
  .movie_box
  .selected_movie
  .about_movie
  .movie_info
  .movie_info_1,
.movie_info_2 {
  font-size: 13px;
}

#container
  .column_left
  .movie_box
  .selected_movie
  .about_movie
  .movie_info
  .running_time::before,
.public_date::before,
.movie_GPA::before {
  background-color: #3a3b3d;
  display: inline-block;
  width: 1px;
  height: 12px;
  margin: 0 7px 0 7px;
  content: "";
  vertical-align: -1px;
}

#container
  .column_left
  .movie_box
  .selected_movie
  .about_movie
  .movie_info
  .movie_info_2 {
  margin: 10px 0;
}

#container .column_left .movie_box .selected_movie .about_movie .movie_info p {
  margin-top: -5px;
}

#container
  .column_left
  .movie_box
  .selected_movie
  .about_movie
  .movie_info
  .movie_info_3 {
  margin-top: 1px;
  line-height: 23px;
  text-overflow: ellipsis;
  overflow: hidden;
  word-wrap: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 7;
  -webkit-box-orient: vertical;
}

#container
  .column_left
  .movie_box
  .selected_movie
  .about_movie
  .movie_info
  .movie_info_3
  .story_content {
  font-size: 12px;
}

#container .column_left .movie_box .selected_movie .delete_movie {
  position: absolute;
  bottom: 15px;
  right: 20px;
  cursor: pointer;
}

#container .column_left .movie_box .selected_movie .movie_info_detail {
  position: absolute;
  top: 15px;
  right: 20px;
}

#container
  .column_left
  .movie_box
  .selected_movie
  .movie_info_detail
  .to_detail {
  color: rgb(117, 117, 117);
  font-size: 11px;
  font-weight: 700;
}
