#container .column_fix_wrap .github_repo {
  border: 1px solid #3a3b3d;
  height: 212px;
  margin-top: 15px;
}

#container .column_fix_wrap .github_repo .github {
  position: relative;
  background-color: #2c2d30;
  padding: 9px 9px;
  text-align: center;
  vertical-align: middle;
  font-weight: 700;
}

#container .column_fix_wrap .github_repo .github .repo_regist_btn {
  position: absolute;
  bottom: 5px;
  right: 5px;
  cursor: pointer;
}

#container .column_fix_wrap .github_repo .repository .list_repo {
  margin-block-start: 0px;
  margin-block-end: 0px;
  height: 174px;
  overflow-y: auto;
}

#container
  .column_fix_wrap
  .github_repo
  .repository
  .list_repo::-webkit-scrollbar {
  width: 5px;
}

#container
  .column_fix_wrap
  .github_repo
  .repository
  .list_repo::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #4d4d4e;
}

#container
  .column_fix_wrap
  .github_repo
  .repository
  .list_repo::-webkit-scrollbar-track {
  background-color: rgba(29, 23, 23, 0.63);
}

#container
  .column_fix_wrap
  .github_repo
  .repository
  .list_repo
  .repo_item:hover {
  background-color: rgb(218, 217, 217);
  color: black;
}

#container .column_fix_wrap .github_repo .repository .list_repo .repo_item {
  position: relative;
  border-bottom: 1px solid #3a3b3d;
  padding: 10.8px;
  font-weight: bold;
}

#container
  .column_fix_wrap
  .github_repo
  .repository
  .list_repo
  .repo_item
  .repo_name {
  margin-left: 10px;
  cursor: pointer;
}

#container
  .column_fix_wrap
  .github_repo
  .repository
  .list_repo
  .repo_item
  .delete_repo {
  position: absolute;
  font-size: 13px;
  top: 12px;
  right: 10px;
  color: #ff0000;
  cursor: pointer;
}
