#wrap #container .info_section {
  position: relative;
  height: 618px;
}

#wrap #container .info_section .info {
  position: absolute;
  background-color: #292a2d;
  top: 18%;
  left: 31%;
  width: 400px;
  height: 300px;
  border: 1px solid #3a3b3d;
  padding: 10px;
}

#wrap #container .info_section .info .info_header {
  text-align: center;
}

#wrap #container .info_section .info .info_header h3 {
  margin-block-start: 0;
}

#wrap #container .info_section .info .info_content {
  font-weight: 600;
}

#wrap #container .info_section .info .info_content input {
  padding: 3px 5px;
  background-color: #1e1f21;
  border-radius: 6px;
  color: rgb(233, 232, 232);
}

#wrap #container .info_section .info .info_footer {
  position: relative;
  margin-top: 35px;
}

#wrap #container .info_section .info .info_footer .info_btn {
  float: right;
  border-radius: 10px;
  background-color: #1e1f21;
  color: white;
  width: 50px;
  height: 25px;
  font-size: 13px;
  margin-left: 10px;
  text-align: center;
  font-weight: 600;
  padding: 6px 7px 3px 7px;
}

#wrap #container .info_section .info .info_footer .update_info_btn {
  background-color: #4063bd;
  cursor: pointer;
}

#wrap #container .info_section .info .info_footer .drop_out_btn {
  background-color: #d32424;
  white-space: nowrap;
  cursor: pointer;
}
