.regist_repo_modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 1;
}

.md_hidden {
  display: none;
}

.md_wrap {
  position: fixed;
  top: 35%;
  left: 40%;
  padding: 20px;
  height: 133px;
  width: 310px;
  background-color: rgba(218, 217, 217, 0.9);
  color: black;
  border-radius: 3px;
}

.regist_repo_modal .md_wrap div {
  margin-top: 10px;
}

.regist_repo_modal .md_header h4 {
  margin-block-start: 0em;
}

.regist_repo_modal .md_wrap .md_footer {
  margin-top: 20px;
  margin-right: -7px;
}

.regist_repo_modal input {
  outline: none;
  display: block;
  border-radius: 5px;
  width: 40%;
  padding: 3px 5px;
}

.regist_repo_modal .md_content .repo_name {
  margin-top: 10px;
}

.md_close_btn {
  /* right: 8px; */
  color: #ff0000;
}

.md_regist_btn {
  /* right: 25px; */
  margin-right: 5px;
  color: #53b900;
}

.md_btn {
  /* position: absolute;
  bottom: 5px; */
  cursor: pointer;
  float: right;
}
