#container .movie_search_box {
  margin-top: 10px;
  margin-bottom: 76px;
}

#container .movie_search_box .search_section {
  position: relative;
  /* border-bottom: 1px solid #3a3b3d; */
}

#container .movie_search_box .search_section .search_input {
  margin-bottom: 20px;
  border-radius: 20px;
  padding: 5px 15px;
  font-size: 13px;
  font-weight: 600;
  width: 200px;
  outline: none;
}

#container .movie_search_box .search_section .movie_search_btn {
  position: absolute;
  top: 4px;
  left: 200px;
  background-color: white;
  cursor: pointer;
  outline: none;
}

#container .movie_search_box .search_result {
  width: 100%;
  height: 486px;
}

#container .movie_search_box .search_result.searched {
  border: 1px solid #3a3b3d;
  height: 484px;
}

#container .movie_search_box .search_result .search_result_section {
  float: left;
  width: 25%;
  height: 485px;

  /* border-left: 1px solid #3a3b3d; */
}

#container .movie_search_box .search_result .search_result_section.searched {
  border-right: 1px solid #3a3b3d;
}

#container
  .movie_search_box
  .search_result
  .search_result_section
  .search_result_list {
  margin-block-end: 0px;
  margin-block-start: 0px;
  height: 485px;
  overflow-y: auto;
}

#container
  .movie_search_box
  .search_result
  .search_result_section
  .search_result_list::-webkit-scrollbar {
  width: 5px;
}

#container
  .movie_search_box
  .search_result
  .search_result_section
  .search_result_list::-webkit-scrollbar-thumb {
  background-color: #3a3b3d;
  border-radius: 10px;
}

#container
  .movie_search_box
  .search_result
  .search_result_section
  .search_result_list::-webkit-scrollbar-track {
  background-color: #292a2d;
}

#container .movie_search_box .search_result .movie_detail_loading {
  position: absolute;
  top: 55%;
  right: 37%;
}

#container .movie_search_box .search_result .selected_movie_detail {
  position: relative;
  float: left;
  width: 69%;
  height: 425px;
  padding: 30px;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_title
  span.mv_title {
  font-weight: 900;
  font-size: 25px;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_title
  span.mv_title::after {
  background-color: #3a3b3d;
  display: inline-block;
  width: 1px;
  height: 12px;
  margin: 0 7px 0 7px;
  content: "";
  vertical-align: -1px;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_content {
  margin-top: 30px;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_left {
  float: left;
  padding-top: 30px;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_right {
  float: left;
  width: 500px;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_right
  .search_movie_info {
  margin-left: 30px;
  padding: 8px 0 0 30px;
  font-size: 14px;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_right
  .search_movie_info
  .mv_spec {
  float: left;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_right
  .search_movie_info
  dt,
dd {
  margin-top: 10px;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_right
  .search_movie_info
  .mv_story {
  margin-top: 35px;
  margin-left: -50px;
  margin-inline-start: 0px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 8;
  -webkit-box-orient: vertical;
  word-wrap: break-word;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_right
  .search_movie_info
  .mv_story
  .mv_story_title {
  font-size: 15px;
  font-weight: 600;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_right
  .search_movie_info
  .mv_story
  .mv_story_content {
  margin-top: -5px;
  font-size: 14px;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_right
  .search_movie_info
  .after_divline::after {
  background-color: #3a3b3d;
  display: inline-block;
  width: 1px;
  height: 12px;
  margin: 0 7px 0 7px;
  content: "";
  vertical-align: -1px;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .search_movie_right
  .search_movie_info
  .rating {
  float: right;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .register_movie_btn {
  position: absolute;
  top: 10px;
  right: 0px;
}

#container
  .movie_search_box
  .search_result
  .selected_movie_detail
  .register_movie_btn
  button {
  margin-right: 10px;
  outline: none;
  cursor: pointer;
}
