#container .column_left .weather {
  background-color: rgb(177, 178, 178);
  position: relative;
  width: 750px;
  height: 135px;
  border: 1px solid #2c2d30;
}

.bird {
  background-image: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/174479/bird-cells-new.svg);
  background-size: auto 100%;
  width: 88px;
  height: 125px;
  will-change: background-position;

  animation-name: fly-cycle;
  animation-timing-function: steps(10);
  animation-iteration-count: infinite;
}

.bird--one {
  animation-duration: 1s;
  animation-delay: -0.5s;
}

.bird--two {
  animation-duration: 0.9s;
  animation-delay: 0s;
}

.bird-container {
  position: absolute;
  top: 10%;
  left: 6%;
  transform: scale(0) translateX(-10vw);
  will-change: transform;

  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.bird-container--one {
  animation-name: fly-right-one;
  animation-duration: 12s;
  animation-delay: 0;
}

.bird-container--two {
  animation-name: fly-right-two;
  animation-duration: 10s;
  animation-delay: 1s;
}

@keyframes fly-cycle {
  100% {
    background-position: -900px 0;
  }
}

@keyframes fly-right-one {
  0% {
    transform: translateY(-5vh) scale(0.2) translateX(-28vw);
  }

  40% {
    transform: translateY(-2vh) translateX(20vw) scale(0.4);
  }

  80% {
    transform: translateY(-5vh) translateX(41.9vw) scale(0.3);
  }

  81% {
    transform: translateY(-5vh) translateX(42vw) scale(0);
  }

  100% {
    transform: translateY(-5vh) translateX(42.1vw) scale(0);
  }
}

@keyframes fly-right-two {
  0% {
    transform: scale(0.2) translateX(-28vw);
  }

  20% {
    transform: translateY(2vh) translateX(7vw) scale(0.3);
  }

  40% {
    transform: translateY(1vh) translateX(18vw) scale(0.3);
  }

  75% {
    transform: translateY(2vh) translateX(33vw) scale(0.3);
  }

  90% {
    transform: translateY(1vh) translateX(42vw) scale(0.2);
  }

  91% {
    transform: translateY(1vh) translateX(42.1vw) scale(0);
  }

  100% {
    transform: translateY(1vh) translateX(42.1vw) scale(0);
  }
}
